import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import myImg from "../../Assets/webimg (1).png";
import myImg2 from "../../Assets/webimg (4).png";
import Tilt from "react-parallax-tilt";
import { SiGmail } from "react-icons/si";
import {
  BsTelegram,
  BsSkype,
} from "react-icons/bs";

function Home2() {
  return (
    <Container fluid className="home-about-section" id="about">
      <Container>
        <Row>
          <Col md={7} className="home-about-description">
            <h1 style={{ fontSize: "2.6em" }}>
              I <span className="purple"> SHOW UP </span>
            </h1>
            <p className="home-about-body">
              <h3 className="purple">I am a proactive and passionate 2D artist.</h3>
              Creative artist with a recognized talent for conveying personalities, emotions, weight, and motion.
              Collaborates well with others to generate and execute ideas within tight deadlines. Earned a reputation
              for dedication, humor, intelligence, and going “above and beyond” to help team-mates.
              <br />
              <br />
              - Experience in the position of 2D Artist at least 3 years;<br />
              - Experience in creating characters, environments, objects and game elements with detailed drawing in a realistic style;<br />
              - Proficiency in Adobe Photoshop;<br />
              - Experience with Adobe Illustrator;<br />
              - Experience in developing graphical user interface (GUI) elements;
              <br />
            </p>
          </Col>
          <Col md={5} className="myAvtar">
            <Tilt>
              <img src={myImg} className="img-fluid" alt="avatar" />
            </Tilt>
          </Col>

          <Col md={5} className="myAvtar">
            <Tilt>
              <img src={myImg2} className="img-fluid" alt="avatar" />
            </Tilt>
          </Col>
          <Col md={7} className="home-about-description">
            <p className="home-about-body">
              <h3 className="purple">I am an experienced 3D designer.</h3>
              - Experience in creating 3D models for rendering in pseudo-isometry;<br />
              - Understanding art styles and 3D mobile games;<br />
              - Understanding the principles of creating high-quality detailed shapes and textures of the model, knowledge of stylized character and animal anatomy;<br />
              - Knowledge how to create objects in pseudo-isometry, for rendering, but the mesh still needs to be created not high-poly, so that you can easily edit it later;<br />
              - Knowledge of the Modo tool (Maya is acceptable at the first stages), basic graphics tools (Photoshop, Zbrush, optionally Substance Painter);
              <br />
            </p>
          </Col>
        </Row>
        <Row>
          <Col md={12} className="home-about-social">
            <h1>FIND ME ON</h1>
            <p>
              Feel free to <span className="purple">contact </span>with me
            </p>
            <ul className="home-about-social-links">
              <li className="social-icons">
                <a
                  href="mailto:bruicanlas@gmail.com"
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <SiGmail />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href="https://t.me/Attwice"
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <BsTelegram />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href="https://t.me/Attwice"
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <BsSkype />
                </a>
              </li>
            </ul>
          </Col>
        </Row>
      </Container>
    </Container>
  );
}
export default Home2;
