import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { SiGmail } from "react-icons/si";
import { 
  BsTelegram,
  BsSkype,
 } from "react-icons/bs";

function Footer() {
  let date = new Date();
  let year = date.getFullYear();
  return (
    <Container fluid className="footer">
      <Row>
        <Col md="4" className="footer-copywright">
          <h3>Copyright @ {year} Steven Lee</h3>
        </Col>
        <Col md="4" className="footer-copywright">
          <h3>The template is made by © Steven Lee</h3>
        </Col>
        <Col md="4" className="footer-body">
          <ul className="footer-icons">
            <li className="social-icons">
                <a
                  href="mailto:bruicanlas@gmail.com"
                  target="_blank"
                  rel="noreferrer"
                  style={{color : "white"}}
                >
                  <SiGmail />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href="https://t.me/Attwice"
                  target="_blank"
                  rel="noreferrer"
                  style={{color : "white"}}
                >
                  <BsTelegram />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href="https://t.me/Attwice"
                  target="_blank"
                  rel="noreferrer"
                  style={{color : "white"}}
                >
                  <BsSkype />
                </a>
              </li>
          </ul>
        </Col>
      </Row>
    </Container>
  );
}

export default Footer;
