import React from "react";
import Particle from "../Particle";
import {
  Row,
  Col,
  Container,
} from "react-bootstrap";


function Projects() {
  return (
    <Container fluid className="project-section">
      <Particle />
      <h1 className="text-white my-5">10K NFT Art Design</h1>
      <Row className="bg-dark p-4 purple">
        <Col md={4}>
          <a href="https://stevenlee.imgbb.com/albums" alt="none">
            <img
              className="d-block w-100 sliderimg"
              src="../projects/4.png"
              alt="Zero slide"
            />
          </a>
        </Col>
        <Col md={4}>
          <a href="https://stevenlee.imgbb.com/albums" alt="none">
            <img
              className="d-block w-100 sliderimg"
              src="../projects/5.png"
              alt="Zero slide"
            />
          </a>

        </Col>
        <Col md={4}>
          <a href="https://stevenlee.imgbb.com/albums" alt="none">
            <img
              className="d-block w-100 sliderimg"
              src="../projects/6.png"
              alt="Zero slide"
            />
          </a>
        </Col>
      </Row>
      <h1 className="text-white my-5">2D and 3D game art</h1>
      <Row className="bg-dark p-4 purple">
        <Col md={4}>
          <a href="https://stevenlee.imgbb.com/albums" alt="none">
            <img
              className="d-block w-100 sliderimg"
              src="../projects/1.png"
              alt="Zero slide"
            />
          </a>
        </Col>
        <Col md={4}>
          <a href="https://stevenlee.imgbb.com/albums" alt="none">
            <img
              className="d-block w-100 sliderimg"
              src="../projects/2.png"
              alt="Zero slide"
            />
          </a>

        </Col>
        <Col md={4}>
          <a href="https://stevenlee.imgbb.com/albums" alt="none">
            <img
              className="d-block w-100 sliderimg"
              src="../projects/3.png"
              alt="Zero slide"
            />
          </a>
        </Col>
      </Row>
    </Container>
  );
}

export default Projects;
